* {
  margin: 0;
  padding: 0;
}
html,
body {
  height: 100%;
  width: 100%;
}
li {
  list-style: none;
}
#react {
  height: 100%;
  width: 100%;
  overflow: hidden;
  height: 1080px;
  width: 1920px;
}
#react > .main {
  height: 100%;
  width: 100%;
  position: relative;
  background: -webkit-gradient(linear, left top, left bottom, from(#aeaeae), to(#808089));
  background: linear-gradient(180deg, #aeaeae 0%, #808089 100%);
}
#react > .main ol,
#react > .main ul,
#react > .main dl {
  margin-bottom: 0;
}
@-webkit-keyframes moveIn {
  from {
    -webkit-transform: translateX(-100vw);
            transform: translateX(-100vw);
  }
  to {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}
@keyframes moveIn {
  from {
    -webkit-transform: translateX(-100vw);
            transform: translateX(-100vw);
  }
  to {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}
@-webkit-keyframes moveOut {
  from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  to {
    -webkit-transform: translateX(100vw);
            transform: translateX(100vw);
  }
}
@keyframes moveOut {
  from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  to {
    -webkit-transform: translateX(100vw);
            transform: translateX(100vw);
  }
}
.chang-enter-active,
.chang-appear-active {
  -webkit-animation: moveIn 3s linear;
          animation: moveIn 3s linear;
  position: absolute;
  top: 0;
  left: 0;
}
.chang-exit-active {
  -webkit-animation: moveOut 3s linear;
          animation: moveOut 3s linear;
  position: absolute;
  top: 0;
  left: 0;
}
.chang-exit-done {
  margin-top: 0;
}
